"use es6";

import { select, selectAll, scaleLinear, max, min, scaleOrdinal } from "d3";

export default class Mindard {
  constructor(rootSelector, data) {
    this.rootSelector = rootSelector;

    this.width = 1500;
    this.height = 750;

    this.xScale = scaleLinear([75, this.width - 75]);
    this.yScale = scaleLinear([50, this.height - 200]);
    this.colorScale = scaleOrdinal(["orange", "grey"]);
    this.lineWidthScale = scaleLinear([5, 150]);
    this.temperatureScaleColor = scaleLinear(["cyan", "black"]);
    this.temperatureScaleLont = scaleLinear([0, 100]);
    this.data = this.processData(data);

    this.canvas = this.rootSelector
      .append("svg:svg")
      .attr("width", this.width)
      .attr("height", this.height)
      .attr("class", "canvas")
      .attr("overflow", "visible")
      .attr("class", "minard-svg");

    this.draw();
  }

  processData(data) {
    let cities = [];
    let temperatures = [];
    let survivors = {
      1: [],
      2: [],
      3: []
    };
    let lines = [];

    this.xScale.domain([
      min(data, d => d.survivors.lonp),
      max(data, d => d.survivors.lonp)
    ]);
    this.yScale.domain([
      max(data, d => {
        if (d.city) return d.city.latc;
        else return d.survivors.latp;
      }),
      min(data, d => {
        if (d.city) return d.city.latc;
        else return d.survivors.latp;
      })
    ]);
    this.lineWidthScale.domain([
      min(data, d => d.survivors.value),
      max(data, d => d.survivors.value)
    ]);

    // Scaling each of the longitude and latitudes and moving data points
    // into appropriate arrays
    data.forEach(d => {
      if (d.city) {
        cities.push(
          Object.assign(d.city, {
            lonc: this.xScale(d.city.lonc),
            latc: this.yScale(d.city.latc)
          })
        );
      }

      if (d.temp) {
        temperatures.push(
          Object.assign(d.temp, {
            lont: this.xScale(d.temp.lont)
          })
        );
      }
      survivors[d.survivors.div].push(
        Object.assign(d.survivors, {
          lonp: this.xScale(d.survivors.lonp),
          latp: this.yScale(d.survivors.latp),
          value: d.survivors.value
        })
      );
    });
    Object.values(survivors).forEach(armyDivision => {
      for (let i = 0; i < armyDivision.length - 1; i++) {
        lines.push({
          x1: armyDivision[i].lonp,
          y1: armyDivision[i].latp,
          x2: armyDivision[i + 1].lonp,
          y2: armyDivision[i + 1].latp,
          size: armyDivision[i].value,
          direction: armyDivision[i].direction
        });
      }
    });

    console.log(max(cities, d => d.latc));

    this.temperatureScaleColor.domain([
      min(temperatures, d => d.value),
      max(temperatures, d => d.value)
    ]);
    this.temperatureScaleLont.domain([
      min(temperatures, d => d.lont),
      max(temperatures, d => d.lont)
    ]);

    return {
      cities,
      temperatures,
      survivors,
      lines
    };
  }

  draw() {
    this.lineGroup = this.canvas.append("svg:g").attr("class", "lines");
    this.lines = this.lineGroup
      .selectAll(".lines")
      .data(this.data.lines)
      .enter()
      .append("svg:line")
      .attr("x1", d => d.x1)
      .attr("y1", d => d.y1)
      .attr("x2", d => d.x2)
      .attr("y2", d => d.y2)
      .attr("opacity", "1")
      .attr(
        "style",
        d =>
          ` stroke:${this.colorScale(
            d.direction
          )};stroke-width:${this.lineWidthScale(d.size)}; stroke-linecap:round;`
      );

    this.lines.append("svg:title").text(d => `Army Size: ${d.size}`);

    function handleMouseHover() {
      select(this)
        .transition()
        .duration(50)
        .attr("opacity", 0.5);
    }

    function handleMouseOff() {
      select(this)
        .transition()
        .duration(50)
        .attr("opacity", 1);
    }

    this.lines.on("mouseover", handleMouseHover).on("mouseout", handleMouseOff);

    // Add cities to the map
    this.cityGroup = this.canvas.append("svg:g").attr("class", "cityGroup");
    this.cities = this.cityGroup
      .selectAll(".cities")
      .data(this.data.cities)
      .enter()
      .append("svg:g")
      .attr("class", "cities");

    this.cities
      .append("svg:text")
      .attr("x", d => d.lonc)
      .attr("y", d => d.latc + 10)
      .attr("text-anchor", "middle")
      .attr("fill", "darkblue")
      .attr("font-size", "1.5em")
      .text(d => d.name);

    this.cities
      .append("svg:ellipse")
      .attr("cx", d => d.lonc)
      .attr("cy", d => d.latc)
      .attr("rx", 70)
      .attr("ry", 30)
      .attr("fill", "none")
      .attr("stroke", "SlateBlue");

    this.tempGradient = this.canvas
      .append("svg:defs")
      .append("svg:linearGradient")
      .attr("id", "gradient")
      .attr("x1", "0%")
      .attr("y1", "0%")
      .attr("x2", "100%")
      .attr("y2", "0%");

    this.tempStops = this.tempGradient
      .selectAll("stop")
      .data(this.data.temperatures.reverse())
      .enter()
      .append("svg:stop")
      .attr("offset", d => `${this.temperatureScaleLont(d.lont)}%`) // lat percentage
      .attr(
        "style",
        d => `stop-color:${this.temperatureScaleColor(d.value)};stop-opacity:1`
      );

    // this.tempGradient
    //   .append("svg:stop")
    //   .attr("offset", d => `0%`) // lat percentage
    //   .attr("style", d => `stop-color:red;stop-opacity:1`);
    // this.tempGradient
    //   .append("svg:stop")
    //   .attr("offset", d => `50.1230928%`) // lat percentage
    //   .attr("style", d => `stop-color:blue;stop-opacity:1`);
    // this.tempGradient
    //   .append("svg:stop")
    //   .attr("offset", d => `100%`) // lat percentage
    //   .attr("style", d => `stop-color:green;stop-opacity:1`);

    this.temperatureGroup = this.canvas
      .append("svg:g")
      .attr("class", "temperatureGroup");

    this.temperatureLine = this.temperatureGroup
      .append("svg:line")
      .attr(
        "x1",
        min(this.data.temperatures, d => d.lont)
      )
      .attr("y1", this.height - 100)
      .attr(
        "x2",
        max(this.data.temperatures, d => d.lont)
      )
      .attr("y2", this.height - 99)
      .attr("stroke", "url(#gradient)")
      .attr("stroke-width", "20");

    this.temperatureGroup
      .append("svg:circle")
      .attr("cx", 650)
      .attr("cy", this.height - 130)
      .attr("r", 10)
      .attr("fill", "cyan");

    this.temperatureGroup
      .append("svg:circle")
      .attr("cx", 730)
      .attr("cy", this.height - 130)
      .attr("r", 10)
      .attr("fill", "black");

    this.temperatureGroup
      .append("text")
      .text("0")
      .attr("font-size", "1.5em")
      .attr("x", 750)
      .attr("y", this.height - 123);

    this.temperatureGroup
      .append("text")
      .text("-30")
      .attr("font-size", "1.5em")
      .attr("x", 665)
      .attr("y", this.height - 123);

    this.temperatureGroup
      .append("text")
      .text("Return Journey Temperature Gradient")
      .attr("font-size", "1.5em")
      .attr("x", "250")
      .attr("y", this.height - 124);

    // Add survivor checkpoints to map
    // this.survivorGroup = this.canvas.append("svg:g").attr("class", "survivors");
    // this.survivors = this.survivorGroup
    //   .selectAll(".survivors")
    //   .data(this.data.survivors[1])
    //   .enter()
    //   .append("svg:circle")
    //   .attr("cx", d => d.lonp)
    //   .attr("cy", d => d.latp)
    //   .attr("r", 5)
    //   .attr("fill", d => this.colorScale(d.div));
  }
}
